
.calculator {
  height: 100%;
  width: 100%;
}
.calculator .input-box {
  height: 120px;
  background: #fff;
  text-align: left;
  font-size: 26px;
  line-height: 40px;
  padding: 10px;
}
.calculator .key-box {
  height: auto;
  overflow: hidden;
  padding: 15px 0;
}
.calculator .key-box span {
  display: inline-block;
  width: 70px;
  height: 60px;
  line-height: 60px;
  font-size: 28px;
  border: 1px solid #d5d5d5;
  margin: 2px;
  border-radius: 5px;
}
.calculator .key-box .icon {
  text-indent: -9999px;
}
.calculator .key-box .icon01 {
  background: url(../assets/icon_01.png) no-repeat center center;
  background-size: 50%;
}
.calculator .key-box .icon02 {
  background: url(../assets/icon_02.png) no-repeat center center;
  background-size: 50%;
}
.calculator .key-box .icon03 {
  background: url(../assets/icon_03.png) no-repeat center center;
  background-size: 50%;
}
.calculator .key-box .icon04 {
  background: url(../assets/icon_04.png) no-repeat center center;
  background-size: 50%;
}
.calculator .key-box .icon05 {
  background: url(../assets/icon_05.png) no-repeat center center;
  background-size: 40%;
}
.calculator .key-box .icon06 {
  background: url(../assets/icon_06.png) no-repeat center center;
  background-size: 40%;
}



